import axios from 'axios';

// ajax 基础数据
import { HOST, commonParam } from './config';

// 投票列表
export function getVoteList(option) {
  const url = HOST + '/weixin/vote/lists';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}
// 投票详情
export function getVoteDetail(option) {
  const url = HOST + '/weixin/vote/detail';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}

