<template>
  <div class="vote-detail" v-show="ready">
    <!-- vote detail panel -->
    <panel page-type="vote"
          :data="detail"
          :status="statusText"
          :is-admin="isAdmin"
    />
    <!-- vote form -->
    <div class="form" v-if="!isAdmin || isPreview">
      <div class="type" :class="isRadioCheck ? 'radio': 'multi'">{{formTitle}}</div>
      <form class="question-form" @submit.prevent ref="form">
        <ul class="options">
          <li v-for="(item, index) in questionInfo.option" :key="index">
            <input
              :type="isRadioCheck ? 'radio' : 'checkbox'"
              name="answer"
              :id="`check_${index}`"
              :checked="item.checked && detail.msg"
              :value="index + 1"
              :disabled="detail.status == 4 && detail.msg"
            >
            <label :for="`check_${index}`">
              <span class="icon"></span>
              <span>{{thumbs[index]}}、</span>
              <span>{{item.title}}</span>
            </label>
          </li>
        </ul>
        <div class="btn" v-if="!detail.msg && detail.status != 3 || isPreview">
          <button type="submit" @click="checkForm">提交</button>
        </div>
      </form>
    </div>
    <!-- link to statistic -->
    <div class="statistics" v-if="isAdmin && !isPreview" @click="toMemberList">
      <span class="icon"></span>
      <span class="not">未参与:　{{joinCount.notJoin}}人</span>
      <span class="had">已参与:　{{joinCount.hadJoin}}人</span>
      <span class="arrow">></span>
    </div>
    <!-- vote progress -->
    <div class="progress" v-if="isAdmin && !isPreview">
      <div class="top">
        <span class="icon" :class="isRadioCheck ? 'radio': 'multi'"></span>
        <span class="type">{{isRadioCheck ? '单选' : '多选'}}</span>
      </div>
      <ul class="lists">
        <li class="item" v-for="(item, index) in statistics" :key="index">
          <p class="title">{{item.title}}</p>
          <div class="bar">
            <span class="sum">得票：{{item.count}}</span>
            <progress class="pro" :value="item.rate" max="100"></progress>
            <span class="per">{{item.rate}}%</span>
          </div>
        </li>
      </ul>
    </div>
    <!-- 统计页面 -->
    <transition name="slide-left">
      <keep-alive>
        <router-view class="router"></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script type="ecmascript-6">
import Panel from '@/base/panel/vote-survey-detail';

import { getVoteDetail, submitVote, getVoteStatistics } from '@/api/vote';
import { AID, ERR_OK, LOGIN_EXPIRE } from '@/api/config';

import { VOTE_STATE_TEXT, THUMBS } from '@/common/js/config';
import { loadVoteCount, saveVoteCount } from '@/common/js/cache';

import { mapMutations, mapGetters } from 'vuex';

export default {
  data() {
    return {
      // 详情面板
      detail: {
        status: 0
      },
      // 投票是单选？
      isRadioCheck: false,
      // 投票问题
      questionInfo: {
        option: []
      },
      // 投票统计 -- 管理员
      statistics: [],
      // 投票参与人数 -- 管理员
      joinCount: {
        notJoin: 0,
        hadJoin: 0,
        canRemind: false
      },
      // 当前是否预览页 -- 管理员
      isPreview: false
    }
  },
  created() {
    // 判断是否是预览页
    if (this.$route.meta.preview) {
      this.isPreview = true;
    }
    // 数据就绪
    this.ready = false;
    // 投票状态
    this.statusText = VOTE_STATE_TEXT;
    // 问题序号
    this.thumbs = THUMBS;
    // 请求投票详情
    this._getDetail();

    // 请求投票统计 -- 管理员
    if (this.isAdmin) {
      this._getStatistics();
    }
  },
  computed: {
    // 多选现限制
    checkLimit() {
      if (this.isRadioCheck) {
        return null;
      } else {
        if (this.questionInfo && 'extra' in this.questionInfo) {
          return JSON.parse(this.questionInfo.extra);
        } else {
          return {};
        }
      }
    },
    // 表单标题
    formTitle() {
      if (this.isRadioCheck) {
        return '单选';
      } else {
        // 补丁 -- 预览多选时不显示选项限制(后端无预览接口)
        return `多选` + (this.isPreview ? '' : `【可选择${this.checkLimit.least}-${this.checkLimit.most}项】`);
      }
    },
    ...mapGetters(['isAdmin'])
  },
  methods: {
    // 获取投票详情
    _getDetail() {
      const voteId = this.$route.params.id;
      getVoteDetail({ id: voteId }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.detail = res.data.data.detail;

          // 详情接口后端未给出管理员身份标志，前端根据返回字段判断
          if ('unjoin_mem_count' in res.data.data && !this.isAdmin) {
            this.setUserType({ isAdmin: true });

            this._getStatistics();
          }

          if (!('unjoin_mem_count' in res.data.data) && this.isAdmin) {
            this.setUserType({ isAdmin: false });
          }

          if (this.isAdmin) {
              this.joinCount.hadJoin = res.data.data.join_mem_count;

              this.joinCount.notJoin = res.data.data.unjoin_mem_count;

              this.joinCount.canRemind = Number(res.data.data.detail.canRemind) === 1;
          } else {
            this.questionInfo = res.data.data.question_info;

            this.isRadioCheck = Number(res.data.data.question_info.type) === 1;
          }

          // 允许多次投票后端未给出已投次数，根据返回数据更新status
          if (Number(this.detail.status) === 4) {
             this.updateVoteStatus({ voteId, status: this.detail.status });
          }

          this.ready = true;

        } else {
          const that = this;
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {
              that.$router.push(`/vote/list/aid/${AID}/`);
            }
          });
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 提交表单
    _submit(param) {
      if (this.throttle) {
        return;
      }
      this.throttle = true;

      const voteId = this.$route.params.id;

      param = Object.assign({}, param, { id: voteId });

      submitVote(param).then((res) => {
          const that = this;
          if (res.data.code === ERR_OK) {

            if (Number(this.detail.num) === 1) {
              this.detail.status = '4';
            } else {
              // 累计投票次数
              let voteCount = loadVoteCount();

              voteCount[voteId] = voteCount[voteId] ? voteCount[voteId] + 1 : 1;

              if (voteCount[voteId] === Number(this.detail.num)) {
                this.detail.status = '4';
              }

              saveVoteCount(voteCount);
            }

            // 允许多次投票后端未给出已投次数，根据返回数据更新status
            this.updateVoteStatus({ voteId, status: this.detail.status });

            this.$dialog.alert({
              mes: res.data.data.finish,
              callback() {
                that.$router.push(`/vote/list/aid/${AID}/`);
              }
            });
          } else {
             const that = this;

             this.$dialog.alert({
              mes: res.data.msg,
              callback() {
                that.throttle = false;
              }
            });
          }
      }).catch((err => {
        console.error(err);
        this.throttle = false;
      }));
    },
     // 校验表单
    checkForm() {
      if (this.isPreview) {

        this.$dialog.alert({
          mes: '当前页面仅供预览！',
          callback() { }
        });

        return;
      }

      let formObj = [];
      let checks = this.$refs.form.querySelectorAll('input');

      for (let i = 0; i < checks.length; i++) {
        if (checks[i].checked) {
          formObj.push(checks[i].value);
        }
      }

      if (!formObj.length) {
        this.$dialog.toast({
          mes: '您还未选择投票项！',
          timeout: 1500
        });

        return;
      }
      if (this.checkLimit && 'least' in this.checkLimit) {
        if (formObj.length > this.checkLimit.most) {
          this.$dialog.toast({
            mes: `请选择不要多于${this.checkLimit.most}项`,
            timeout: 1500
          });

          return;
        } else if (formObj.length < this.checkLimit.least) {
          this.$dialog.toast({
            mes: `请选择不要少于${this.checkLimit.least}项`,
            timeout: 1500
          });

          return;
        }
      }
      formObj = +formObj ? +formObj : String(formObj);

      this._submit({ answer: formObj });
    },
    // 获取统计结果 -- 管理员
    _getStatistics() {
      const voteId = this.$route.params.id;
      getVoteStatistics({ id: voteId }).then((res) => {
        if (res.data.code === ERR_OK) {
          this.statistics = res.data.data.answer;

          this.isRadioCheck = Number(res.data.type) === 1;

          // 补丁，管理员预览页面后端未给接口，从统计中获取调研问题（多选时，无法获取到选项）
          if (this.isPreview) {
            this.questionInfo = this.getQuestion(res.data.data.answer)
          }
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 查看参与人员 -- 管理员
    toMemberList(){
      this.setJoinCount(this.joinCount);
      this.$router.push(`${this.$route.fullPath}/statistics`);
    },
    // 补丁 -- 预览页，从统计接口整理出表单数据
    getQuestion(list) {
      const questionInfo = { option: [] };

      questionInfo.type = this.isRadioCheck ? 1 : 2;

      list.forEach(function(item) {
        questionInfo.option.push({ title: item.title, checked: 0 });
      }, this);

      return questionInfo;
    },
    ...mapMutations({
      updateVoteStatus: 'UPDATE_VOTE_STATUS',
      setJoinCount: 'SET_JOIN_COUNT',
      setUserType: 'SET_USER_TYPE'
    })
  },

  components: {
    Panel,
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";
@import "~@/common/stylus/mixin";
.vote-detail
  position absolute
  top 0
  bottom 0
  left 0
  right 0
  padding-bottom 0.8rem
  background-color #f4f4f4
  -webkit-overflow-scrolling: touch;
  font-size 0
  &::-webkit-scrollbar
    display none
  .form
    position relative
    padding-top 0.2rem
    background-color #fff
    margin 0.2rem 0 2rem
    .type
      height 0.5rem
      padding-left 0.9rem
      line-height 0.5rem
      font-size 0.36rem
      margin-bottom 0.1rem
    .type.multi
      background url("../../assets/multi-check-bg.png") no-repeat 0.2rem 0
      background-size 0.4rem 0.4rem
    .type.radio
      background url("../../assets/radio-check-bg.png") no-repeat 0.2rem 0
      background-size 0.4rem 0.4rem
    .options
      input[type="radio"],
      input[type="checkbox"]
        display none
      input[type="radio"]+label>.icon
        background url("../../common/images/radio.png") no-repeat
        background-size cover
      input[type="checkbox"]+label>.icon
        background url("../../common/images/checkbox.png") no-repeat
        background-size cover
      input[type="checkbox"]:disabled+label>.icon
        background url("../../common/images/checkbox-disabled.png") no-repeat
        background-size cover
      input[type="radio"]:disabled+label>.icon
        background url("../../common/images/radio-disabled.png") no-repeat
        background-size cover
      input[type="checkbox"]:checked+label>.icon
        background url("../../common/images/checkbox-checked.png") no-repeat
        background-size cover
      input[type="radio"]:checked+label>.icon
        background url("../../common/images/radio-checked.png") no-repeat
        background-size cover
      input[type="checkbox"]:checked+label
        background-color #f8ecec
      input[type="radio"]:checked+label
        background-color #f8ecec
      label
        display flex
        align-items center
        min-height 0.8rem
        padding 0.1rem 0
        font-size 0.32rem
        word-break break-all
        &>.icon
          flex-shrink 0
          display inline-block
          width 0.4122rem
          height 0.4122rem
          margin 0 0.25rem 0 0.3rem
    .btn
      position absolute
      width 100%
      bottom -1.7rem
      text-align center
      button
        width 4rem
        height 0.8rem
        border none
        border-radius 0.05rem
        background-color $color-main
        color #fff
        font-size 0.36rem
  .statistics
    display flex
    align-items center
    height 0.88rem
    padding 0 0.2rem
    margin 0.2rem 0
    background-color #fff
    font-size 0.34rem
    .icon
      width 0.4rem
      height 0.4rem
      margin-right 0.2rem
      background url("../../assets/statistic.png") no-repeat
      background-size cover
    .not
      margin-right 0.5rem
    .arrow
      flex-grow 1
      text-align right
      color #aaa
  .progress
    padding 0 0.2rem
    background-color #fff
    .top
      display flex
      align-items center
      height 0.6rem
      .icon
        width 0.4rem
        height 0.4rem
      .icon.radio
        background url("../../assets/radio-check-bg.png") no-repeat
        background-size cover
      .icon.multi
        background url("../../assets/multi-check-bg.png") no-repeat
        background-size cover
      .type
        margin-left 0.2rem
        font-size 0.36rem
    .lists
      .title
        margin 0.1rem 0
        font-size 0.38rem
        word-break break-all
      .bar
        display flex
        align-items center
        padding-bottom 0.15rem
        border-bottom 0.01rem solid #eee
        font-size 0.34rem
        color #777
        .pro
          flex-grow 1
          margin 0 0.15rem
          border 0.01rem solid $color-main
          border-radius 0.3rem
          &::-webkit-progress-inner-element
            border-radius 0.3rem
          &::-webkit-progress-value
            background-color $color-main
            border-radius 0.3rem
          &::-webkit-progress-bar
            background-color #fff
            border-radius 0.3rem
  animation-slide-left(0.4s)

</style>




