<template>
  <div class="panel" :class="[pageType]">
    <p class="title">{{data.title}}</p>
    <div class="stamp">
      <span class="time">{{data.create_time}}</span>
      <span class="author">{{data.author}}</span>
      <span class="status" :status="data.status">{{status[data.status]}}</span>
    </div>
    <p class="tip" v-if="data.etime">截至日期：{{data.etime}}</p>
    <p class="tip" v-if="pageType === 'vote' && showTips">{{ VoteLimitTip }}</p>
    <div class="detail-content">
      <div class="des" v-html="data.desc"></div>
      <p class="tip" v-show="!isAdmin">{{statusTip}}</p>
    </div>
  </div>
</template>

<script type="ecmascript-6">
export default {
  props: {
    pageType: {
      type: String,
      default: 'vote',
      required: true
    },
    data: {
      type: Object,
      required: true
    },
    // 状态名称
    status: {
      type: Array,
      required: true
    },
    isAdmin: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  data() {
    return {
      // 参与之后的提示
      tips: '',
      // 显示投票限制的提示
      showTips: false
    };
  },
  computed: {
    VoteLimitTip() {
      let voteType = Number(this.data.type);

      return `本投票${voteType == 2 ? '每天' : ''}只能投${this.data.num}次`;
    },
    statusTip() {
      if (this.pageType === 'survey') {
        if (Number(this.data.status) === 3) {
          return '您已参与';
        }
      } else if (this.pageType === 'vote') {
        if (Number(this.data.status) === 4) {
          this.showTips = false;
          return this.data.msg;
        } else {
          this.showTips = true;
          return '';
        }
      }
    }
  },
  methods: {

  }
};
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";

.panel
  padding 0.2rem
  background-color #fff
  box-shadow 0 2px 8px 1px #ddd
  &.survey .stamp .status[status="3"]
    color $color-main
  .title
    margin-bottom 0.18rem
    font-size 0.42rem
    text-align justify
    word-break break-all
  .stamp
    display flex
    align-items center
    margin-bottom 0.28rem
    font-size 0.32rem
    color #777
    .author
      margin-left 0.5rem
    .status
      flex-grow 1
      text-align right
      font-size 0.28rem
      &[status="0"]
        color #fd7c14
      &[status="1"]
        color #2dc56d
      &[status="2"]
        color #d62525
      &[status="3"]
        color #b42632
      &[status="4"]
        color #b42632
  .tip
    margin-top 0.24rem
    font-size 0.34rem
    color #777
  .detail-content
    padding 0.3rem 0
    font-size 0.36rem
    .des
      width 100%
      overflow-x hidden
      line-height 1.4
      word-break break-all
      text-align justify
    .tip
      color $color-main
      text-align center
    .btn
      text-align center
      color #777

</style>



